import {
	Flex,
	Container,
	Heading,
	Stack,
	Text,
	Button,
	Icon,
	Box,
} from "@chakra-ui/react";
import { Image } from "@chakra-ui/react";
import Typewriter from "typewriter-effect";
import "./homepage.css";
import { Divider } from "@chakra-ui/react";
import rocket from "./rocket.png";
import { useMediaQuery } from "@chakra-ui/react";
export default function Homepage() {
	const [isLargerThan1280] = useMediaQuery("(min-width: 796px)");
	return (
		<div className="bg-img" id="home">
			<div className="orange-circle" />
			<Container maxW={"6xl"} marginTop={"100px"}>
				<Stack
					textAlign={"left"}
					align={"left"}
					spacing={{ base: 8, md: 10 }}
					py={{ base: 20, md: 28 }}>
					<Heading
						fontWeight="bold"
						fontSize={{ base: "4xl", sm: "4xl", md: "6xl" }}
						lineHeight={"140%"}
						color={"white"}>
						Hello, my name is
						<Text
							bgGradient="linear(to-r, #0cffdf, #8d0eb4)"
							bgClip="text"
							fontSize={{ base: "5xl", sm: "4xl", md: "6xl" }}
							fontWeight="extrabold"
							lineHeight={"140%"}>
							Sarthak Ahuja
						</Text>
						<Text
							bgGradient="linear(to-l, #12c2e9, #c471ed, #f64f59)"
							bgClip="text"
							fontSize={{ base: "4xl", sm: "4xl", md: "6xl" }}>
							<Typewriter
								options={{
									strings: [" Full Stack Developer"],
									autoStart: true,
									loop: true,
								}}
							/>
						</Text>
					</Heading>

					<Stack spacing={6} direction={"row"}>
						<Button
							// rounded={"full"}
							px={8}
							color={"Black"}
							bg={"#12c2e9"}
							fontWeight={"bold"}
							_hover={{ bg: "orange.500" }}>
							RESUME
						</Button>
					</Stack>
				</Stack>
				<Box></Box>
				<div>
					{isLargerThan1280 ? (
						<Image
							src={rocket}
							className="rocket"
							style={{ height: "250px" }}
						/>
					) : (
						<Image
							src={rocket}
							className="rocket"
							style={{ height: "300px" }}
						/>
					)}
				</div>
				{/* <Divider height="100px" orientation="horizontal" /> */}
			</Container>
		</div>
	);
}
