import React from "react";
import { Heading, Image } from "@chakra-ui/react";
import "./aboutme.css";
import { Divider } from "@chakra-ui/react";
import Typewriter from "typewriter-effect";
const AboutMe = () => {
	return (
		<div className="abt-me" id="about">
			<div className="white-grad" />
			<Heading className="hline" p={"30"} color={"black"}>
				<Typewriter
					options={{
						strings: ["< AboutMe />"],
						autoStart: true,
						loop: true,
					}}
				/>
			</Heading>

			<div className="detail">
				<Image
					className="img"
					borderRadius="full"
					boxSize="100px"
					src="https://bit.ly/dan-abramov"
					alt="Dan Abramov"
				/>
			</div>

			<Divider height="100px" width="50%" m="auto" orientation="horizontal" />
		</div>
	);
};

export default AboutMe;
